.root {
  background: linear-gradient(155.84deg, #24375f -37.58%, #000000 61.78),
    #0a0d18;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
  border-radius: 10px;

  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  color: white;
}

.title {
  margin: 0;
  color: #6f99eb;
  font-family: "Gilroy";
  font-size: 1rem;
}

.link {
  margin: 0;
  font-weight: bold;

  display: flex;
  align-items: center;
  font-size: 0.875rem;
  cursor: pointer;

  & > svg {
    width: 0.875rem;
  }
}

.content {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  border-radius: 8px;
  margin-top: 20px;
  padding: 15px;

  display: flex;
  justify-content: space-between;
}
