.root {
  width: 100%;
  flex-grow: 1;

  display: flex;
  flex-direction: column;
}

.header {
  border-radius: 30px 30px 0 0;
  background: #150a30;
  padding: 8px;
}

.body {
  background: #3b275b;
  border-radius: 0 0 30px 30px;
  padding: 20px 20px 30px;
  flex-grow: 1;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;

  font-family: "Gilroy";

  @media (max-width: 400px) {
    flex-direction: row;
  }
}

.lockWrapper {
  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0.7;
  color: white;
  font-weight: bold;
  font-size: 0.75rem;
  line-height: 0.875rem;

  & * {
    color: white;
    max-height: 20px;
    max-width: 20px;
  }
}

.img {
  width: 100px;
  height: 100px;
  margin: 20px 0;
}

.title {
  margin: 0;
  color: white;
  font-size: 1rem;
  text-align: center;
  line-height: 1.1875rem;
  text-transform: uppercase;
}

.text {
  margin: 0;
  margin-top: 5px;

  color: #ededed;
  text-align: center;
  font-size: 0.75rem;
  line-height: 0.875rem;
}

.textWrapper {
  @media (max-width: 400px) {
    margin-left: 20px;
  }
}
