.root {
  background: #150a30;
  width: 100%;
  height: 100%;
  border-radius: 30px;

  padding: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
}

.title {
  text-transform: uppercase;
  font-family: "Gilroy";
  font-size: 1.125rem;
  line-height: 21px;

  color: white;
  margin: 0;
}

.text {
  font-family: "Gilroy";
  font-size: 1rem;
  line-height: 19px;
  color: #e9e9e9;
  margin: 10px 0;
}

.textBlock {
}

.button {
  background: linear-gradient(
    323.1deg,
    #acb0f2 -130.1%,
    #3f398f 40.98%
  ) !important;
  box-shadow: inset 0px -2px 0px 1px rgba(0, 0, 0, 0.135107) !important;
  border-radius: 25px;

  font-family: "Gilroy";
  font-size: 0.9375rem;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: white;
  padding: 10px 40px;
}
