.root {
  min-height: 500px;
  max-width: 515px;
  width: 100%;
  display: flex;
  font-family: "Gilroy";
  grid-area: slider;
  background: #150a30;
  position: relative;
  border-radius: 30px;
  padding: 30px;
}

.title {
  color: #d8e6fa;
  font-size: 1.5rem;
  line-height: 15px;
  text-align: center;
  margin: 20px;
}

.text {
  color: white;
  font-size: 1rem;
  text-align: center;
  max-width: 320px;
  display: block;
  margin: 0 auto;
}
.cardContainer {
  display: grid;
}

.img {
  width: 250px;
  height: 250px;
  margin-left: auto;
  margin-right: auto;
}

.arrowContainer {
  position: absolute;
  top: 140px;
  z-index: 1;
}

.leftContainer {
  @extend .arrowContainer;
  left: 50px;
}
.rightContainer {
  @extend .arrowContainer;
  right: 50px;
}

.arrow {
  width: 30px;
  height: 30px;
  transition: all 0.4s !important;
  &:hover {
    transform: scale(1.2);
  }
}

.arrowRight {
  @extend .arrow;
  right: 80%;
  transform: rotate(180deg);
  &:hover {
    transform: rotate(180deg) scale(1.2);
  }
}
