.root {
  grid-area: user;
  background: #150a30;
  width: 100%;
  height: 100%;
  border-radius: 30px;

  padding: 20px;

  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 300px;
}

.link {
  font-family: "Gilroy";
  font-size: 1.125rem;
  line-height: 1.3125rem;
  color: white;

  display: flex;

  position: absolute;
  bottom: 10px;
  right: 10px;

  cursor: pointer;
}
