@import "sass/index";

.root {
  height: 330px;
  width: 100%;

  background: #{use(--primary-main)} url(/assets/banners/levelBackground.webp);
  background-blend-mode: lighten;
  background-position: 100% 0%;
  background-repeat: no-repeat;
  background-size: contain;

  border-radius: 30px;
  padding: 35px;

  display: flex;
  align-items: center;

  margin-bottom: 20px;

  @media (max-width: 700px) {
    background-position: 300px 0%;
  }
}

.content {
  width: calc(100% - 340px);
  max-width: 500px;
  font-family: "Gilroy";

  @media (max-width: 700px) {
    max-width: 300px;
    width: 100%;
  }
}

.title {
  font-size: 2rem;
  line-height: 1;
  margin: 0;
  text-transform: uppercase;
  color: #defdff;

  margin-bottom: 20px;
}

.text {
  font-size: 1rem;
  line-height: 19px;
  color: use(--template-modal-title-color);
  opacity: 0.5;
  margin: 0;
}
