$phoneLevelsTableGrid: 0.3fr 1fr 1fr 0.5fr;

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px;
}

.title {
  text-transform: uppercase;
  line-height: 1.6875rem;
  font-size: 1.375rem;
  font-family: "Gilroy";

  color: white;
  margin: 0;
}

.sliderWrapper {
  & > div {
    background: transparent;
    margin: 0 auto;

    @media (max-width: 400px) {
      padding: 0;
    }
  }
}

.row {
  display: grid;
  grid-template-rows: auto;
  justify-items: center;
  align-items: center;

  min-width: auto;
  grid-template-columns: $phoneLevelsTableGrid;
}

.headerItem {
  font-family: "Gilroy";
  font-weight: bold;
  color: #9f8dd0;

  line-height: 1.0625rem;
  font-size: 0.875rem;
  letter-spacing: 1px;
  margin: 10px 2.5px;

  text-align: center;

  @media (max-width: 400px) {
    font-size: 0.75rem;
  }
}
