$flexBoxMargin: 5px;

.root {
  margin-top: 15px;
}

.textFlexWrapper {
  display: flex;
  margin-bottom: $flexBoxMargin;
}

.text {
  margin: 0;

  font-family: "Gilroy";
  font-size: 0.75rem;
  line-height: 15px;
  color: #9f8dd0;
  font-weight: bold;
}

.textSpan {
  color: white;
}

.separator {
  margin: 0 $flexBoxMargin;
}
