.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 600px) {
    margin: 30px;
  }
}

.title {
  text-transform: uppercase;
  line-height: 1.6875rem;
  font-size: 1.375rem;
  font-family: "Gilroy";

  color: white;
  margin: 0;
}

.closeIcon {
  fill: white;
  cursor: pointer;
}

.sliderWrapper {
  & > div {
    background: transparent;
    margin: 0 auto;

    @media (max-width: 400px) {
      padding: 0;
    }
  }
}
