$tableWidth: 600px;
$levelsTableGrid: 0.3fr minmax(130px, 0.5fr) 0.5fr 1fr 0.5fr;

.root {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;

  min-height: 300px;
  height: 100%;
}

.row {
  display: grid;
  grid-template-columns: $levelsTableGrid;
  grid-template-rows: auto;
  justify-items: center;
  align-items: center;
  min-width: $tableWidth;
}

.headerItem {
  font-family: "Gilroy";
  font-weight: bold;
  color: #9f8dd0;

  line-height: 1.0625rem;
  font-size: 0.875rem;
  letter-spacing: 1px;
  margin: 10px 2.5px;

  text-align: center;

  @media (max-width: 400px) {
    font-size: 0.75rem;
  }
}

.table {
  display: flex;
  flex-direction: column;

  overflow: auto;
  min-width: $tableWidth;
}

.bottom {
  height: 30px;
  width: 100%;
}

.loaderWrapper {
  display: block;
  width: min-content;
  margin: 50px auto 0;
}
