.title {
  font-family: "Gilroy";
  font-size: 1.625rem;
  line-height: 31px;
  text-transform: uppercase;
  color: #defdff;
  margin: 20px 0;
  text-align: center;
  width: fit-content;
}

.wrapper {
  display: grid;
  justify-content: space-evenly;
  grid-gap: 20px;
  gap: 20px;
}

.cards {
  grid-template-columns: repeat(auto-fit, 200px);
  @media (max-width: 400px) {
    display: flex;
    flex-direction: column;
  }
}

.exclusive {
  grid-template-columns: repeat(auto-fit, 350px);
}
