$fSize: 16px;

.root {
  background: rgba(#ffffff, 0.05);
  border-radius: 10px;
  padding: 20px;

  width: 100%;
}

.avatar {
  height: 120px;
  width: 120px;
}

.rewardWrapper {
  position: relative;
  width: fit-content;
  margin: 0 auto;
}

.rewardImg {
  & > svg {
    height: 100px;
    width: 100px;
  }
}

.rewardImgWrapper {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: -40px;
}

.rating {
  position: absolute;
  top: 50px - $fSize;
  left: 0;

  margin: 0;

  opacity: 0.4;
  color: black;
  width: 100%;
  text-align: center;

  font-family: "Gilroy";
  font-weight: 900;
  font-size: $fSize;
}

.name {
  font-family: "Gilroy";
  font-size: 1.375rem;
  line-height: 1.6875rem;

  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  color: white;
  margin: 20px 0 5px;
}

.ratingWrapper {
  margin: 0 auto;
  width: fit-content;
}

.iconEmpty {
  color: #ffb400;
  opacity: 0.3;
}
