$arrowHeight: 9px;
$arrowWidth: 6px;

.root {
  width: 100%;
  margin-top: 20px;
}

.rewardLine {
  background: linear-gradient(
    162.43deg,
    #ffefdc 42.71%,
    #fff3e3 58.1%,
    #ffead1 67.5%,
    #f0dbc0 81.82%,
    #f0dbc2 93.72%
  );

  border-radius: 3px;
  width: 100%;
  height: 5px;
  margin-top: 10px;

  position: relative;
}

.rewardArrow {
  position: absolute;
  top: 1px - $arrowHeight;

  width: 0;
  height: 0;
  border-left: $arrowWidth solid transparent;
  border-right: $arrowWidth solid transparent;

  border-bottom: $arrowHeight solid #ffead1;

  transition: transform 0.4s;
  transform-origin: 0%;
}
