.root {
  display: flex;
  width: fit-content;

  align-items: center;
  flex-shrink: 0;
}

.avatar {
  height: 36px;
  width: 36px;
}

.nameWrapper {
  margin-left: 10px;
}

.name {
  text-transform: uppercase;
  font-family: "Gilroy";
  font-size: 1.3rem;
  font-weight: bold;
  line-height: 27px;
  color: white;
  margin: 0;
}

.img {
  width: 38px;
  height: 38px;
}

.iconEmpty {
  color: #ffb400;
  opacity: 0.3;
}

.icon {
  margin: 0 -2.5px;
}
