$phoneLevelsTableGrid: 0.3fr 1fr 1fr 0.5fr;

.row {
  display: grid;
  grid-template-columns: $phoneLevelsTableGrid;

  justify-items: center;
  align-items: center;

  border-top: 1px solid #2e2b4a;
  word-wrap: break-word;
  word-break: break-all;

  opacity: 0.5;

  height: 100%;

  &:last-child {
    border-bottom: 1px solid #2e2b4a;
  }
}

.rowActive {
  opacity: 1 !important;
}

.text {
  font-family: "Gilroy";
  color: white;

  font-size: 1rem;
  line-height: 1.1875rem;
  margin: 10px 5px;
  text-align: center;
  @media (max-width: 400px) {
    font-size: 0.79rem;
  }
}

.img {
  height: 50px;
  width: 50px;
  object-fit: contain;
}
