$width: 530px;

.modalRoot {
  border-radius: 30px;
  overflow: hidden;

  width: 100%;
  max-width: $width;
  // min-width: 100% !important;

  @media (max-width: 600px) {
    border-radius: 0;
    max-width: 100%;
  }
}

.root {
  background: linear-gradient(
      128.34deg,
      #4e82d6 -25.58%,
      rgba(11, 4, 21, 0.181709) 32.6%,
      rgba(3, 1, 4, 0.0001) 60.36%,
      #000000 80.79%
    ),
    #0a0d18;
  width: 100%;
  max-width: $width;
  min-height: 400px;
  padding: 30px;
  border-radius: 30px;

  display: flex;
  flex-direction: column;

  @media (max-width: 600px) {
    width: 100%;
    height: 100%;
    max-block-size: 100%;
    border-radius: 0;
    padding: 5px;
  }
}

.scrollbar {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;

  @media (max-width: 600px) {
    margin: 30px;
  }
}

.title {
  text-transform: uppercase;
  line-height: 1.6875rem;
  font-size: 1.375rem;
  font-family: "Gilroy";

  color: white;
  margin: 0;
}

.closeIcon {
  color: white;
  cursor: pointer;

  position: absolute;
  top: 25px;
  right: 25px;
}
