.modalRoot {
  border-radius: 30px;
  overflow: hidden;

  width: 100% !important;
  max-width: 720px;

  @media (max-width: 600px) {
    border-radius: 0;
  }
}

.root {
  background: linear-gradient(
      128.34deg,
      #4e82d6 -25.58%,
      rgba(11, 4, 21, 0.181709) 32.6%,
      rgba(3, 1, 4, 0.0001) 60.36%,
      #000000 80.79%
    ),
    #0a0d18;
  width: 100%;
  max-width: 720px;
  min-height: 400px;
  padding: 30px;
  padding-right: 15px;
  border-radius: 30px;

  @media (max-width: 600px) {
    width: 100%;
    height: 100%;
    border-radius: 0;
    padding: 5px;
  }
}

.wrapper {
  width: 100%;
  height: 100%;
  max-height: calc(100vh - 130px);
  min-height: 500px;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  padding-right: 10px;

  @media (max-width: 600px) {
    max-height: calc(100vh - 10px);
    padding-right: 0;
  }
}

.closeIcon {
  fill: white;
  color: white;
  cursor: pointer;

  position: absolute;
  top: 20px;
  right: 30px;
}
