.root {
  position: relative;

  margin: 20px 0;
  padding: 20px;

  width: 100%;
  border-radius: 10px;

  & > :first-child {
    background: transparent;
    position: relative;
    z-index: 1;
    margin: 0;

    & > span > h4 {
      color: #cafca4;
    }
  }
}

.background {
  background: linear-gradient(162.85deg, #cafca4 -64.23%, #000000 88.7%),
    #0a0d18;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
  opacity: 0.5;

  width: 100%;
  height: 100%;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  border-radius: 10px;
}
